.contact-page {
  background-color: #002ea6;
}
@media screen and (min-width: 1025px) {
  .contact-page {
    min-height: calc(41.252vw + 110px);
  }
}
.contact-page strong {
  color: #17fcc4;
}
.contact-page .txt-content {
  width: 100%;
  max-width: 525px;
}
@media (max-width: 1024px) {
  .contact-page .txt-content {
    padding-right: 40px;
  }
}
@media (min-width: 1025px) {
  .contact-page .txt-content {
    max-width: 27.344vw;
  }
}
@media (min-width: 1921px) {
  .contact-page .txt-content {
    max-width: 525px;
  }
}
.contact-page .team-images {
  right: 0;
  top: 10px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  flex-wrap: wrap;
}
@media (max-width: 1024px) {
  .contact-page .team-images {
    display: none;
  }
}
@media (min-width: 1025px) {
  .contact-page .team-images {
    width: 39.063vw;
  }
}
@media (min-width: 1441px) {
  .contact-page .team-images {
    width: 52.083vw;
  }
}
@media (min-width: 1921px) {
  .contact-page .team-images {
    width: 1000px;
  }
}
.contact-page .team-images .single-team-member {
  display: inline-block;
}
.contact-page .team-images .single-team-member img {
  border-radius: 50%;
}
@media (min-width: 1025px) {
  .contact-page .team-images .single-team-member {
    width: 7.708vw;
    height: 7.708vw;
  }
}
@media (min-width: 1441px) {
  .contact-page .team-images .single-team-member {
    width: 10.313vw;
    height: 10.313vw;
  }
}
@media (min-width: 1921px) {
  .contact-page .team-images .single-team-member {
    width: 200px;
    height: 200px;
  }
}
.contact-page .mobile-team-images {
  right: 64px;
  top: 0;
  width: 70px;
}
@media screen and (max-width: 500px) {
  .contact-page .mobile-team-images {
    right: 0;
  }
}
@media (min-width: 1025px) {
  .contact-page .mobile-team-images {
    display: none;
  }
}
.contact-page .mobile-team-images .single-team-member {
  display: inline-block;
  width: 70px;
  height: 70px;
}
.contact-page .mobile-team-images .single-team-member img {
  border-radius: 50%;
}
.contact-page .background-svg-wrap.svg-contact-hero {
  left: 0;
  top: 30px;
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
}
@media (min-width: 1025px) {
  .contact-page .background-svg-wrap.svg-contact-hero {
    width: 65.625vw;
    height: 65.625vw;
  }
}
@media (min-width: 1921px) {
  .contact-page .background-svg-wrap.svg-contact-hero {
    width: 1260px;
    height: 1260px;
  }
}
@media (min-width: 1025px) {
  .contact-page .background-svg-wrap.svg-contact-hero .background-flower-path-svg {
    width: 32.813vw;
    height: 32.813vw;
  }
}
@media (min-width: 1921px) {
  .contact-page .background-svg-wrap.svg-contact-hero .background-flower-path-svg {
    width: 630px;
    height: 630px;
  }
}
.contact-page .background-svg-wrap.svg-contact-hero .st0 {
  fill: none;
  stroke-miterlimit: 10;
}
.contact-page .background-svg-wrap.svg-contact-hero .svg-full {
  opacity: 0.1;
}
.contact-page .background-svg-wrap.svg-contact-hero .svg-full .st0 {
  fill: #7f96d2;
}
.contact-page .background-svg-wrap.svg-contact-hero .svg-stroke {
  opacity: 0.2;
}
.contact-page .background-svg-wrap.svg-contact-hero .svg-stroke .st0 {
  stroke: #7f96d2;
}
.contact-page .background-svg-wrap.svg-contact-hero .svg-stroke .st1 {
  fill: #7f96d2;
}
@media (max-width: 1024px) {
  .contact-page .background-svg-wrap.svg-contact-hero {
    display: none;
  }
}
