}
.contact-tabs .testimonial-content-tab {
  display: none;
}
.contact-tabs .testimonial-content-tab.show {
  display: block;
}
.contact-tabs .tabs-nav {
  z-index: 3;
  /*
  @media ($mobile_all) {
      margin-bottom: 40px;
  }
  */
}
.contact-tabs .tabs-nav li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0 !important;
  background-color: #fff;
  border-bottom: 1px solid #002ea6;
  /*
  @media ($min1024) {
      width: 350px;
  }

  @media ($min1440) {
      width: 425px;
  }

  &:hover,
  &:focus {
      cursor: pointer;
  }
  */
}
.contact-tabs .tabs-nav li a {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 20px;
}
.contact-tabs .tabs-nav li a {
  background-color: #17fcc4;
  transition: background-color 0.3s ease;
}
.contact-tabs .tabs-nav li a.inactive {
  background-color: transparent;
  transition: background-color 0.3s ease;
}
.contact-tabs .tabs-nav li a span {
  padding-right: 20px;
  transition: padding-right 0.3s ease;
}
.contact-tabs .tabs-nav li a:hover span, .contact-tabs .tabs-nav li a:focus span {
  padding-right: 10px;
}
.contact-tabs .user-testimonial {
  display: flex;
  /*
  @media ($min1024) {
      width: 350px;
  }

  @media ($min1440) {
      width: 425px;
  }
  */
  background-color: #f2f4fa;
  padding: 25px;
}
.contact-tabs .user-testimonial .user-img {
  flex: 0 0 auto;
  margin-right: 25px;
}
@media (max-width: 767px) {
  .contact-tabs .user-testimonial .user-img {
    width: 60px;
  }
}
.contact-tabs .user-testimonial .testimonial-content img {
  height: 24px;
  margin-bottom: 15px;
}
.contact-tabs .user-testimonial .testimonial-content .txt-size-18 {
  margin-bottom: 25px;
}
.contact-tabs .tab-content .tab-content-heading {
  font-weight: 700;
}
.contact-tabs .tab-content .tab-subheading {
  font-weight: 700;
  margin: 25px 0px;
}
@media (min-width: 1025px) {
  .contact-tabs .tab-contacts {
    margin-bottom: 40px;
  }
}
.contact-tabs .tab-content-wrap {
  display: none;
}
.contact-tabs .tab-content-wrap.show {
  display: block;
}
.contact-tabs .single-tab-contact {
  margin-bottom: 15px;
  display: flex;
  align-items: center;
}
.contact-tabs .single-tab-contact img {
  margin-right: 30px;
}
.contact-tabs .contacts-tab-form .form-styles .form-label {
  background-color: #fff;
  color: #002ea6;
}
.contact-tabs .contacts-tab-form .form-styles .form-input {
  color: #002ea6;
}
.contact-tabs .contacts-tab-form .hbspt-form .submitted-message.hs-main-font-element {
  color: #002ea6 !important;
}